import React, { useEffect, useState } from 'react';
import fetch from 'cross-fetch';
import Alert from '@mui/material/Alert';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import InputAdornment from '@mui/material/InputAdornment';
import InputLabel from '@mui/material/InputLabel';
import LinearProgress from '@mui/material/LinearProgress';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Snackbar from '@mui/material/Snackbar';
import Stack from '@mui/material/Stack';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import AddIcon from '@mui/icons-material/Add';
import AdminLayout from '@components/admin/Layout';
import ClientsPartnersTableRow from '@components/admin/invitations/ClientsPartnersTableRow';

const AdminInvitationsIndexPage = () => {
  const [isOpenSnackbar, setIsOpenSnackbar] = useState(false);
  const [severity, setSeverity] = useState('info');
  const [message, setMessage] = useState('');

  const [partners, setPartners] = useState([]);
  const [clientsPartners, setClientsPartners] = useState([]);

  // 新規作成フォーム
  const [newClientName, setNewClientName] = useState('');
  const [newPartnerId, setNewPartnerId] = useState('');
  const [newClientNote, setNewClientNote] = useState('');
  const [isDisabledNewButton, setIsDisabledNewButton] = useState(true);

  const [reload, setReload] = useState(0);

  // @TODO 仮のUsername
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');

  const handleCloseSnackbar = (e, reason) => {
    if (reason === 'clickaway') return;
    setIsOpenSnackbar(false);
  };

  const handleChangeNewClientNote = ({ target: { value } }) =>
    setNewClientNote(value);

  useEffect(() => {
    const f = async () => {
      try {
        const resPartners = await fetch(`${process.env.API_URL}/api/partners`);
        const resClientsPartners = await fetch(
          `${process.env.API_URL}/api/partners/clients`
        );

        if (resPartners.status >= 400) {
          throw new Error('Bad response /api/partners');
        }

        if (resClientsPartners.status >= 400) {
          throw new Error('Bad response /api/partners/clients');
        }

        const partnersData = await resPartners.json();
        const clientsPartnersData = await resClientsPartners.json();

        setPartners(partnersData);
        setClientsPartners(clientsPartnersData);
      } catch (e) {
        console.log(e);
      }
    };

    f();
  }, [reload]);

  return (
    <AdminLayout setUsername={setUsername} setEmail={setEmail}>
      <Typography variant='h6'>
        紹介システム 権限付与<small>（DARUMA → オーナー様）</small>
      </Typography>
      <Box
        display='flex'
        my={2}
        pl={2}
        py={4}
        sx={{
          border: '2px solid #ccc',
          borderRadius: 2
        }}
      >
        <TextField
          label='顧客名'
          variant='outlined'
          value={newClientName}
          InputProps={{
            endAdornment: <InputAdornment position='end'>様</InputAdornment>
          }}
          onChange={e => {
            const _newClientName = e.target.value;
            setNewClientName(_newClientName);
            setIsDisabledNewButton(!newPartnerId || !_newClientName);
          }}
        />
        <FormControl
          sx={{
            minWidth: 120
          }}
        >
          <InputLabel>担当者</InputLabel>
          <Select
            label='担当者'
            value={newPartnerId}
            onChange={e => {
              const _newPartnerId = e.target.value;

              setNewPartnerId(_newPartnerId);
              setIsDisabledNewButton(!_newPartnerId || !newClientName);
            }}
          >
            {partners.map(
              (
                { uuid, lastNameKanji, firstNameKanji, lastName, firstName },
                i
              ) => {
                const partnerName =
                  lastNameKanji && firstNameKanji
                    ? `${lastNameKanji} ${firstNameKanji}`
                    : `${lastName} ${firstName}`;

                return (
                  <MenuItem key={i} value={uuid}>
                    {partnerName}
                  </MenuItem>
                );
              }
            )}
          </Select>
        </FormControl>
        <TextField
          label='メモ'
          variant='outlined'
          multiline
          value={newClientNote}
          onChange={handleChangeNewClientNote}
          sx={{
            minWidth: 320
          }}
        />
        <Button
          variant='contained'
          color='primary'
          startIcon={<AddIcon />}
          disabled={isDisabledNewButton}
          onClick={async () => {
            try {
              const res = await fetch(
                `${process.env.API_URL}/api/partners/clients`,
                {
                  method: 'POST',
                  body: JSON.stringify({
                    name: newClientName,
                    partnerId: newPartnerId,
                    note: newClientNote,
                    createdBy: username
                  })
                }
              );

              if (res.status >= 400) {
                throw new Error('Bad response /api/partners/clients');
              }

              setIsOpenSnackbar(true);
              setSeverity('success');
              setMessage(`${newClientName} 様の情報を追加しました`);

              setNewClientName('');
              setNewPartnerId('');
              setNewClientNote('');
              setIsDisabledNewButton(true);

              setReload(reload + 1);
            } catch (e) {
              alert('error');
              console.log(e);
            }
          }}
        >
          新規作成
        </Button>
      </Box>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>URL</TableCell>
              <TableCell>顧客名</TableCell>
              <TableCell>担当者</TableCell>
              <TableCell>メモ</TableCell>
              <TableCell>操作</TableCell>
              <TableCell>タイムスタンプ</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {!clientsPartners[0] ? (
              <TableRow>
                <TableCell colSpan={6}>
                  <Typography align='center'>Loading...</Typography>
                  <LinearProgress />
                </TableCell>
              </TableRow>
            ) : (
              clientsPartners.map((clientsPartner, i) => (
                <ClientsPartnersTableRow
                  key={i}
                  clientsPartner={clientsPartner}
                  partners={partners}
                  username={username}
                  email={email}
                  reload={() => setReload(reload + 1)}
                />
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>
      <Stack spacing={2} sx={{ width: '100%' }}>
        <Snackbar
          open={isOpenSnackbar}
          autoHideDuration={5000}
          onClose={handleCloseSnackbar}
        >
          <Alert severity={severity}>{message}</Alert>
        </Snackbar>
      </Stack>
    </AdminLayout>
  );
};

export default AdminInvitationsIndexPage;
