import React, { useEffect, useState } from 'react';
import { navigate } from 'gatsby';
import { jwtDecode } from 'jwt-decode';
import Alert from '@mui/material/Alert';
import Avatar from '@mui/material/Avatar';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import LogoutIcon from '@mui/icons-material/Logout';

const localStorageKeyName = 'com.daruma-inv.admin';
const CLIENT_ID =
  '137389090265-9p2asf68ustrc46nemdmmi8d4l5gnapm.apps.googleusercontent.com';
// const SECRET_KEY = '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';

const AdminLayout = props => {
  const { children } = props;

  const [isAuthed, setIsAuthed] = useState(false);
  const [isError, setIsError] = useState(false);

  const [email, setEmail] = useState('');
  const [familyName, setFamilyName] = useState('');
  const [givenName, setGivenName] = useState('');
  const [name, setName] = useState('');
  const [picture, setPicture] = useState('');

  const clearStates = () => {
    setEmail('');
    setFamilyName('');
    setGivenName('');
    setName('');
    setPicture('');
    props.setUsername && props.setUsername('');
    props.setEmail && props.setEmail('');
  };

  useEffect(() => {
    const loginGoogleAccounts = () => {
      window.google.accounts.id.initialize({
        client_id: CLIENT_ID,
        callback: ({ credential: credentialJwt }) => {
          const { email } = jwtDecode(credentialJwt);

          if (
            /@daruma-inv.com$/.test(email) ||
            email === 'koujirou0111@gmail.com' ||
            email === 'oxwm.system@gmail.com'
          ) {
            window.localStorage.setItem(localStorageKeyName, credentialJwt);
            setIsAuthed(true);
          } else {
            setIsError(true);
            setIsAuthed(false);
            return;
          }
        }
      });
      window.google.accounts.id.renderButton(
        document.getElementById('buttonDiv'),
        { theme: 'outline', size: 'large' }
      );
      window.google.accounts.id.prompt();
    };

    const appendGoogleAccounts = () => {
      const script = document.createElement('script');
      script.src = 'https://accounts.google.com/gsi/client';
      script.addEventListener('load', loginGoogleAccounts);
      document.body.appendChild(script);
    };

    const checkLogin = () => {
      const credentialJwt = window.localStorage.getItem(localStorageKeyName);

      // 未ログイン
      if (!credentialJwt) {
        clearStates();
        setIsAuthed(false);
        appendGoogleAccounts();
        return;
      }

      const credential = jwtDecode(credentialJwt);

      // invalid or expired
      if (!credential) {
        clearStates();
        setIsAuthed(false);
        appendGoogleAccounts();
        return;
      }

      // ログイン済み
      setEmail(credential.email);
      setFamilyName(credential.family_name);
      setGivenName(credential.given_name);
      setName(credential.name);
      setPicture(credential.picture);
      setIsError(false);
      setIsAuthed(true);
      props.setUsername && props.setUsername(credential.name);
      props.setEmail && props.setEmail(credential.email);
    };

    document.readyState === 'complete'
      ? checkLogin()
      : window.addEventListener('load', checkLogin);

    return () => {
      window.removeEventListener('load', checkLogin);
    };
  }, [isAuthed]);

  const handleClickLogout = () => {
    window?.google?.accounts?.id?.disableAutoSelect();
    window.localStorage.removeItem(localStorageKeyName);
    clearStates();
    setIsAuthed(false);
  };

  return !isAuthed ? (
    <>
      <Container
        sx={{
          width: '100vw',
          height: '100vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        <Box my={2}>
          <Typography variant='h6'>DARUMA ADMIN TOOLS</Typography>
        </Box>
        <Box>
          <Box
            id='buttonDiv'
            sx={{
              display: 'flex',
              justifyContent: 'center'
            }}
          ></Box>
          <Alert
            severity='error'
            sx={{
              opacity: isError ? 1 : 0,
              pointerEvents: 'none'
            }}
          >
            この Google アカウントのログインは許可していません。
          </Alert>
        </Box>
      </Container>
    </>
  ) : (
    <>
      <AppBar position='fixed' sx={{ backgroundColor: '#333' }}>
        <Container maxWidth='xl'>
          <Toolbar disableGutters>
            <Typography
              variant='h6'
              noWrap
              component='div'
              fontWeight='bold'
              sx={{ mr: 2, display: 'flex' }}
              onClick={() => navigate('/admin/')}
            >
              DARUMA ADMIN TOOLS
            </Typography>
            <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
              <Button
                sx={{ color: '#fff' }}
                onClick={() => navigate('/admin/invitations/')}
              >
                紹介システム
              </Button>
              <Button
                sx={{ color: '#fff', textTransform: 'none' }}
                onClick={() => {
                  const url =
                    'https://analytics.google.com/analytics/web/#/p299554539/reports/intelligenthome';

                  window.open(url, '_blank', 'noreferrer');
                }}
              >
                Google Analytics<small>（アクセス解析）</small>
              </Button>
              <Button
                sx={{ color: '#fff', textTransform: 'none' }}
                onClick={() => {
                  const url =
                    'https://search.google.com/search-console?resource_id=sc-domain:daruma-inv.com';

                  window.open(url, '_blank', 'noreferrer');
                }}
              >
                Google Search Console<small>（SEO解析）</small>
              </Button>
            </Box>
            <Button
              variant='contained'
              color='error'
              startIcon={<LogoutIcon />}
              onClick={handleClickLogout}
              sx={{
                mx: 2
              }}
            >
              ログアウト
            </Button>
            <Box display='flex' flexDirection='row' alignItems='center'>
              <Box
                display='flex'
                flexDirection='column'
                alignItems='center'
                justifyContent='center'
              >
                <Typography fontSize={14} fontWeight='bold'>
                  {name}
                </Typography>
                <Typography fontSize={10}>({email})</Typography>
              </Box>
              <Avatar
                src={picture}
                sx={{
                  ml: 1
                }}
              />
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <Container
        maxWidth='xl'
        sx={{
          mt: 10
        }}
      >
        {children}
      </Container>
    </>
  );
};

export default AdminLayout;
